import { useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import styles from './Horario.module.css';
import Loading from '../layout/Loading';
import Container from '../layout/Container';
import HorarioForm from '../horario/HorarioForm';
import Message from '../layout/Message';

function Horario() {
  const { id } = useParams();
  const [horario, setHorario] = useState({});
  const [showHorarioForm, setShowHorarioForm] = useState(false);
  const [message, setMessage] = useState('');
  const [type, setType] = useState('success');

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`http://localhost:3000/horarios/${id}`);
        const data = await response.json();
        console.log('Dados do hor치rio:', data);
        if (data) {
          setHorario(data);
        } else {
          setMessage('Hor치rio n칚o encontrado!');
          setType('error');
        }
      } catch (error) {
        console.error("Erro ao buscar hor치rio:", error);
        setMessage('Erro ao buscar o hor치rio!');
        setType('error');
      }
    };

    fetchData();
  }, [id]);

  const editPost = async (updatedHorario) => {
    try {
      console.log("Atualizando hor치rio com:", updatedHorario);
      const response = await fetch(`http://localhost:3000/horarios/${id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(updatedHorario),
      });

      const data = await response.json();
      console.log("Resposta da atualiza칞칚o:", data);

      if (response.ok) {
        setHorario(updatedHorario);
        setShowHorarioForm(false);
        setMessage('Hor치rio atualizado com sucesso!');
        setType('success');
      } else {
        setMessage('Erro ao atualizar o hor치rio!');
        setType('error');
      }
    } catch (error) {
      console.error("Erro ao atualizar hor치rio:", error);
      setMessage('Erro ao atualizar o hor치rio!');
      setType('error');
    }
  };

  const deleteHorario = async () => {
    try {
      const response = await fetch(`http://localhost:3000/horarios/${id}`, {
        method: 'DELETE',
      });

      if (response.ok) {
        setMessage('Hor치rio exclu칤do com sucesso!');
        setType('success');
      } else {
        setMessage('Erro ao excluir o hor치rio!');
        setType('error');
      }
    } catch (error) {
      console.error("Erro ao excluir hor치rio:", error);
      setMessage('Erro ao excluir o hor치rio!');
      setType('error');
    }
  };

  return (
    <>
      {message && <Message type={type} msg={message} />}
      {horario.name ? (
        <div className={styles.horario_details}>
          <Container customClass="column">
            <div className={styles.details_container}>
              <h1>Hor치rio: {horario.name}</h1>
              <button className={styles.btn} onClick={() => setShowHorarioForm(!showHorarioForm)}>
                {showHorarioForm ? 'Fechar' : 'Editar hor치rio'}
              </button>
              <button className={styles.btn} onClick={deleteHorario}>
                Excluir hor치rio
              </button>
              <div className={styles.horario_info}>
                {!showHorarioForm ? (
                  <>
                    <p>
                      <span>Categoria:</span> {horario.category || 'Sem categoria'}
                    </p>
                    <p>
                      <span>Hor치rio:</span> {horario.horarios || 'N칚o dispon칤vel'}
                    </p>
                  </>
                ) : (
                  <HorarioForm
                    handleSubmit={editPost}
                    btnText="Concluir edi칞칚o"
                    horarioData={horario}
                  />
                )}
              </div>
            </div>
          </Container>
        </div>
      ) : (
        <Loading />
      )}
    </>
  );
}

export default Horario;
