import { useState, useEffect } from 'react';
import Container from '../layout/Container';
import Loading from '../layout/Loading';
import LinkButton from '../layout/LinkButton';
import HorarioCard from '../horario/HorarioCard';
import Message from '../layout/Message';
import styles from './Horarios.module.css';

function Horarios() {
  const [horarios, setHorarios] = useState([]);
  const [removeLoading, setRemoveLoading] = useState(false);
  const [horarioMessage, setHorarioMessage] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('http://localhost:3000/horarios', {
          credentials: 'same-origin',
        });

        if (!response.ok) {
          throw new Error('Erro ao buscar hor치rios');
        }

        const data = await response.json();
        console.log('Hor치rios recebidos:', data);  
        
        // Ordena os hor치rios por data/hora
        const sortedHorarios = data.sort((a, b) => {
          const dateA = new Date(a.horarios); 
          const dateB = new Date(b.horarios);
          return dateA - dateB; 
        });

        setHorarios(sortedHorarios);
      } catch (error) {
        console.error("Erro ao buscar hor치rios:", error);
        setErrorMessage(error.message);
      } finally {
        setRemoveLoading(true);
      }
    };

    fetchData();
  }, []);

  const removeHorario = async (id) => {
    try {
      const response = await fetch(`http://localhost:3000/horario/${id}`, {
        method: 'DELETE',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'same-origin',
      });

      if (!response.ok) {
        throw new Error('Erro ao remover hor치rio');
      }

      setHorarios((prevHorarios) => prevHorarios.filter((horario) => horario._id !== id));
      setHorarioMessage('Hor치rio removido com sucesso!');
    } catch (error) {
      console.error("Erro ao remover hor치rio:", error);
      setErrorMessage(error.message);
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.title_container}>
        <h1>Meus Hor치rios</h1>
        <LinkButton to="/newhorario" text="Montar hor치rio" customClass={styles.addHorarioButton} />
      </div>
      {horarioMessage && <Message type="success" msg={horarioMessage} />}
      {errorMessage && <Message type="error" msg={errorMessage} />}
      <Container customClass={styles.container}>
        {horarios.length > 0 ? (
          horarios.map((horario) => (
            <HorarioCard
              id={horario._id}
              name={horario.name}
              horario={horario.horarios}
              category={horario.category || 'Sem categoria'}
              key={horario._id}
              handleRemove={removeHorario}
              customClass={styles.horarioCard}
            />
          ))
        ) : (
          !removeLoading ? <Loading /> : <p>N칚o h치 hor치rios cadastrados!</p>
        )}
      </Container>
    </div>
  );
}

export default Horarios;
